@use 'base/variables' as v;
@use 'base/mixins' as m;

.pasos {
	margin-top: 5rem;
	overflow: hidden;
	&__contenido {
		background-color: v.$primario;
	}
	&__heading {
		text-align: center;
		color: v.$blanco;
		&--descripcion {
			font-size: 1.8rem;
			display: block;
		}
	}
	&__grid {
		@include m.tablet {
			@include m.grid(3, v.$separacion);
		}
	}
	&__imagen {
		transition-property: transform;
		transition-duration: 0.3s;
		&:hover {
			transform: rotate(-4deg) scale(1.2);
		}
	}
}
